import Header from './Header';
import Document, { Head, Main, NextScript } from 'next/document';

export default class MyDocument extends Document {
  render() {
    return (
      <html>
        <Head>
          <link rel="stylesheet" href="/_next/static/base.css" />
        </Head>
        <body>
            <Header />
            <section className="content">
                {props.children}
            </section>
        </body>
      </html>
    )
  }
};